.mc-blast {
  text-align: center;
  background: #0078bc;
  color: white;
  padding: 0.65em 3em;
}
.mc-blast > div {
  display: inline-block;
}
.mc-blast[data-level=warn],
.mc-blast[data-level=error] {
  background: #ff3b00;
}
.mc-blast[data-follow=true] .mc-blast-body {
  cursor: pointer;
  text-decoration: underline;
}
.mc-blast-close {
  position: absolute;
  top: 6px;
  right: 6px;
}
