.mc-chart {
  vertical-align: top;
  margin: 0 1em 1em 0;
}
.mc-legend {
  vertical-align: middle;
  color: #7e8890;
  font-size: .9em;
}
.mc-legend[data-flow=across] li {
  display: inline-block;
  margin-right: 1em;
}
.mc-legend[data-flow=down] {
  display: inline-block;
}
.mc-legend[data-flow=down] li {
  margin-bottom: 0.25em;
}
.mc-legend svg {
  margin-right: 5px;
}
.mc-legend li {
  white-space: nowrap;
}
.mc-legend li[role=checkbox] {
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.mc-legend li[aria-checked=false] {
  opacity: 0.4;
  filter: grayscale(100%);
  text-decoration: line-through;
}
