.mc-spinner {
  pointer-events: none;
  position: fixed;
  bottom: 10px;
  left: 10px;
  color: white;
  padding: .5em;
  opacity: 0.8;
  border-radius: 2px;
  background: #e0002b;
  z-index: 11000;
  -webkit-transition: opacity 150ms linear;
  -moz-transition: opacity 150ms linear;
  -ms-transition: opacity 150ms linear;
  transition: opacity 150ms linear;
}
.mc-spinner[aria-hidden=true] {
  opacity: 0;
}
