* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: .9em;
  background: #7e8890;
}
body,
input,
select,
textarea,
button {
  font-family: Helvetica, Arial, sans-serif;
}
svg {
  outline: none;
}
h1,
h2,
h3,
h4 {
  font-family: Helvetica, Arial, sans-serif;
  color: #304561;
}
h1 {
  font-size: 1.65em;
  font-weight: normal;
  margin-bottom: .75em;
}
h2 {
  font-size: 1em;
  color: #0078bc;
  text-transform: uppercase;
  margin-top: .25em;
  margin-bottom: .6em;
}
h3,
h4 {
  font-size: 1em;
  margin-bottom: .5em;
}
h1 + p {
  margin: -0.75em 0 1.5em 0;
}
button {
  font-size: inherit;
  background: none;
  border: none;
}
button:disabled {
  opacity: 0.25;
  cursor: not-allowed;
}
input:disabled,
select:disabled,
textarea:disabled {
  cursor: not-allowed;
}
p {
  margin-bottom: 1em;
  line-height: 1.5em;
}
a,
button {
  text-decoration: none;
  cursor: pointer;
  color: #0078bc;
}
a:visited,
button:visited {
  color: #0078bc;
}
a:hover {
  text-decoration: underline;
}
ul {
  list-style-type: none;
}
small {
  color: rgba(58, 54, 100, 0.5);
}
img {
  border: 0;
}
img[tabindex] {
  cursor: pointer;
}
.mc-hint {
  border-bottom: 1px dashed #ccc;
  cursor: default;
}
#mc-header {
  background: white;
  padding: .6em;
  border-bottom: 1px solid #e8e8e8;
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
}
#mc-content {
  background: #f8f8f8;
  min-height: 500px;
  padding: 2em;
}
#mc-footer {
  color: white;
  font-size: .8em;
  padding: 2em 2em 4em 2em;
}
#mc-footer ul {
  margin-bottom: .75em;
}
#mc-footer li {
  display: inline;
}
#mc-footer li:not(:last-child) {
  padding-right: 0.8em;
  margin-right: 0.4em;
  border-right: 1px solid rgba(255, 255, 255, 0.35);
}
#mc-footer a {
  color: white;
}
#mc-env {
  visibility: hidden;
  color: white;
  background: #7ac724;
  font-size: .8em;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: .5em;
  text-transform: uppercase;
}
#mc-feedback {
  font-size: .8em;
  visibility: hidden;
  position: fixed;
  right: 15px;
  bottom: 0;
  border-radius: 4px 4px 0 0;
  background: #ff8309;
}
#mc-feedback button {
  font-size: 1.1em;
  padding: .5em .75em;
  color: white;
}
#mc-feedback button:active {
  padding-bottom: .35em;
}
@media print {
  #mc-header {
    padding: 0;
    margin-bottom: 2em;
  }
  #mc-content {
    padding: 0;
  }
  #mc-footer {
    padding: 1em 0 0 0;
  }
}
