.mc-orderable {
  margin-bottom: 1em;
}
.mc-orderable img {
  pointer-events: none;
}
.mc-orderable-item {
  padding: .5em 0;
  border: 1px solid transparent;
  white-space: nowrap;
}
.mc-orderable-item[data-dragging=true] {
  background: #eee;
  opacity: 0.75;
}
.mc-orderable-item[data-targeted=before] {
  border-top: 1px dashed black;
}
.mc-orderable-item[data-targeted=after] {
  border-bottom: 1px dashed black;
}
.mc-orderable-handle {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  background: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-position: 5px 8px;
  background-size: 20px 15px;
  cursor: move;
  width: 36px;
  height: 30px;
  margin-right: 5px;
  display: inline-block;
  vertical-align: top;
}
.mc-orderable-handle:active {
  transform: scale(0.9);
}
.mc-orderable-content {
  display: inline-block;
  vertical-align: top;
  line-height: 30px;
  width: calc(100% - 50px);
}
