.mc-plot {
  display: block;
  font: 11px Helvetica, sans-serif;
}
.mc-plot > g > rect {
  stroke: rgba(0, 0, 0, 0.12);
  stroke-width: 0.5;
}
.mc-plot-x-axis {
  font: 12px Helvetica, sans-serif;
  pointer-events: none;
}
.mc-plot-x-axis line {
  stroke: rgba(0, 0, 0, 0.12);
  stroke-dasharray: 5, 3;
}
.mc-plot-x-axis path {
  display: none;
}
.mc-plot-x-axis text {
  text-anchor: middle;
}
.mc-plot-x-axis-nav {
  cursor: zoom-in;
}
.mc-plot-x-axis-nav > g {
  visibility: hidden;
  pointer-events: none;
}
.mc-plot-x-axis-nav text {
  fill: white;
  text-anchor: middle;
}
.mc-plot-y-axis {
  font: 12px Helvetica, sans-serif;
  pointer-events: none;
}
.mc-plot-y-axis line {
  stroke: rgba(0, 0, 0, 0.12);
  stroke-dasharray: 5, 3;
}
.mc-plot-y-axis path {
  display: none;
}
.mc-plot-line path {
  fill: none;
  pointer-events: none;
}
.mc-plot-line circle {
  pointer-events: none;
}
.mc-plot-download {
  cursor: pointer;
  opacity: 0.15;
}
.mc-plot-download:hover {
  opacity: 0.75;
}
