.mc-toggle {
  width: 65px;
  height: 28px;
  border-radius: 15px;
  padding: 3px 8px;
  color: white;
  background: #ddd;
  text-align: right;
  text-transform: uppercase;
  font-size: 0.9em;
  position: relative;
  transition: background 100ms linear;
}
.mc-toggle span {
  display: inline-block;
  background: white;
  position: absolute;
  top: 4px;
  left: 4px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: left 100ms linear;
}
.mc-toggle[aria-pressed=true] {
  text-align: left;
  background: #2aceba;
}
.mc-toggle[aria-pressed=true] span {
  left: 41px;
}
.mc-toggle[disabled] {
  opacity: 0.25;
  cursor: not-allowed;
}
