.mc-calendar {
  white-space: nowrap;
}
.mc-calendar-page {
  display: inline-block;
  margin: 0.5em;
}
.mc-calendar-page div:nth-child(1) {
  font-weight: bold;
  color: #304561;
  text-align: center;
}
.mc-calendar-page div:nth-child(2) {
  font-size: 0.8em;
  text-align: center;
  margin: 0.4em 0;
}
.mc-calendar-page div:nth-child(2) span {
  display: inline-block;
  width: 14.285%;
}
.mc-calendar-page g rect {
  fill: none;
}
.mc-calendar-page g[data-day] rect {
  fill: #e8e8e8;
  stroke: white;
  stroke-width: 2;
}
.mc-calendar-page g[data-ts] rect {
  cursor: pointer;
}
.mc-calendar-page g[data-ts]:hover circle {
  fill: #7ac724;
}
.mc-calendar-page g[data-ts]:active circle {
  transform: scale(0.75);
}
.mc-calendar-page g[data-selected=true] circle {
  fill: #7ac724;
}
.mc-calendar-page g circle {
  pointer-events: none;
  fill: #009cff;
  transform-origin: 11px 11px;
}
