.mc-alert-scrim {
  background-color: rgba(0, 0, 0, 0.6);
  text-align: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3000;
}
.mc-alert-scrim[aria-hidden=true] {
  display: none;
}
.mc-alert-dialog {
  color: #304561;
  background-color: white;
  box-shadow: 0 0 7px #444;
  border-radius: 4px;
  padding: 1.5em;
  margin: 50px;
  text-align: center;
  display: inline-block;
  outline: none;
}
.mc-alert-dialog[data-level=warn] .mc-button,
.mc-alert-dialog[data-level=error] .mc-button {
  background: #e0002b;
}
.mc-alert-body {
  text-align: center;
  min-width: 300px;
  max-width: 400px;
  min-height: 75px;
  margin-bottom: 1.5em;
  font-size: 1.2em;
  line-height: 1.4em;
}
.mc-alert-body input,
.mc-alert-body textarea {
  margin: .5em 0;
  background: #f8f8f8;
  max-width: 350px;
}
