.mc-tooltip {
  position: absolute;
  z-index: 10000;
  background: rgba(0, 0, 0, 0.75);
  color: white;
  padding: 8px 9px 6px 9px;
  border-radius: 3px;
  font-size: .85em;
  pointer-events: none;
  white-space: nowrap;
}
.mc-tooltip[aria-hidden=true] {
  visibility: hidden;
}
.mc-tooltip[data-orient=top]:after {
  border-top: 5px solid rgba(0, 0, 0, 0.75);
  bottom: -10px;
}
.mc-tooltip[data-orient=bottom]:after {
  border-bottom: 5px solid rgba(0, 0, 0, 0.75);
  top: -10px;
}
.mc-tooltip[data-align=right]:after {
  left: auto;
  right: 12px;
}
.mc-tooltip:after {
  position: absolute;
  border: 5px solid transparent;
  left: 50%;
  margin-left: -5px;
  content: " ";
}
[data-tooltip] * {
  pointer-events: none;
}
a[data-tooltip] * {
  pointer-events: auto;
}
