.mc-button {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  display: inline-block;
  font-family: Helvetica;
  position: relative;
  cursor: pointer;
  padding: .75em 1.25em;
  color: white;
  background: #0078bc;
  border-radius: 25px;
  margin: 0 .25em;
  line-height: 1em;
}
.mc-button[disabled] {
  opacity: 0.25;
  cursor: not-allowed;
}
.mc-button:not([disabled]):active {
  top: 1px;
}
.mc-button:hover {
  text-decoration: none;
}
.mc-icon-button {
  background: none;
  border: 0;
  border-radius: 0;
  display: inline-block;
  padding: 4px;
  line-height: 0;
}
.mc-icon-button img,
.mc-icon-button svg,
.mc-icon-button span {
  pointer-events: none;
  vertical-align: middle;
}
.mc-icon-button span {
  margin-left: .5em;
  vertical-align: middle;
}
.mc-icon-button + button {
  margin-left: 4px;
}
.mc-icon-button[disabled] {
  opacity: 0.25;
  cursor: default;
  border: 0 !important;
  pointer-events: none;
}
.mc-icon-button[aria-pressed=false] {
  opacity: 0.25;
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
}
.mc-icon-button[data-weight=bold] {
  font-weight: bold;
}
.mc-icon-button:not([disabled]):not([data-display=text]) {
  cursor: pointer;
}
.mc-icon-button:not([disabled]):not([data-display=text]):hover img,
.mc-icon-button:not([disabled]):not([data-display=text]):hover svg {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}
.mc-icon-button:not([disabled]):not([data-display=text]):active img,
.mc-icon-button:not([disabled]):not([data-display=text]):active svg {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
.mc-icon-button[data-display=text]:hover {
  text-decoration: underline !important;
}
.mc-check {
  display: inline-block;
  color: #009cff;
  cursor: pointer;
  margin: 0.25em 0.5em;
}
.mc-check input {
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  background-image: url("../img/mc/icon.check.circle.0.svg");
  background-size: 2.5em 2.5em;
  background-repeat: no-repeat;
  width: 2.5em;
  height: 2.5em;
}
.mc-check input:checked {
  background-image: url("../img/mc/icon.check.circle.1.svg");
}
.mc-check[data-size=medium] input {
  background-size: 1.75em 1.75em;
  width: 1.75em;
  height: 1.75em;
}
.mc-check[data-size=small] input {
  background-size: 1.6em 1.6em;
  width: 1.6em;
  height: 1.6em;
}
.mc-check[data-shape=square] input {
  background-image: url("../img/mc/icon.check.square.0.svg");
}
.mc-check[data-shape=square] input:checked {
  background-image: url("../img/mc/icon.check.square.1.svg");
}
.mc-check span {
  vertical-align: middle;
}
