.mc-table {
  width: 100%;
  border-collapse: collapse;
  font-size: inherit;
  margin-bottom: 1em;
}
.mc-table thead {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  color: #333;
  font-size: .9em;
  border-bottom: 1px solid #f8f8f8;
  white-space: nowrap;
}
.mc-table tbody {
  background: white;
}
.mc-table tbody tr {
  vertical-align: top;
}
.mc-table tbody tr:nth-child(odd) {
  background: #f8f8f8;
}
.mc-table th[data-sortable='true'] {
  cursor: pointer;
  color: #0078bc;
}
.mc-table th[data-sortable='true']:hover {
  background: #eee;
}
.mc-table th[data-sorted]:after {
  content: url("data:image/svg+xml;base64,");
  display: inline-block;
  margin-left: .35em;
  width: 12px;
  height: 12px;
  line-height: 1em;
  -webkit-transition: transform 125ms linear;
  -moz-transition: transform 125ms linear;
  -ms-transition: transform 125ms linear;
  transition: transform 125ms linear;
}
.mc-table th[data-sorted='1']:after {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}
.mc-table th[data-sorted='-1']:after {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
.mc-table th,
.mc-table td {
  text-align: left;
  padding: .6em 1em .6em .6em;
}
.mc-table td img {
  display: inline-block;
  vertical-align: text-bottom;
}
.mc-table td[data-clip=true] {
  max-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.mc-table[data-align=middle] tbody tr {
  vertical-align: middle;
}
.mc-props {
  border-collapse: collapse;
}
.mc-props th,
.mc-props td {
  padding: 0 2em 1em 0;
  vertical-align: top;
}
.mc-props th {
  text-align: left;
  font-size: .9em;
  white-space: nowrap;
}
@media print {
  .mc-table th,
  .mc-table td {
    padding: .25em 0;
    border-bottom: 1px solid #ddd;
    white-space: normal;
    overflow: visible;
  }
}
