.mc-help-view header {
  text-align: center;
  margin-bottom: 1em;
}
.mc-help-view section {
  margin-bottom: 2em;
}
.mc-help-view h1 {
  text-align: center;
  margin-bottom: 1.5em;
}
.mc-help-view h2 {
  color: #304561;
  font-size: 1.1em;
  margin: 1.5em 0 1em 0;
  text-transform: none;
}
.mc-help-view dl {
  margin-bottom: 1em;
  line-height: 1.5em;
}
.mc-help-view dt {
  font-weight: bold;
}
.mc-help-view dd {
  margin-bottom: 1em;
}
