.mc-date-picker {
  text-align: center;
  margin: .25em;
  position: relative;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.mc-date-picker h4 {
  margin: .65em 1em;
  cursor: default;
}
.mc-date-picker table {
  margin: .5em;
  font-size: inherit;
}
.mc-date-picker th {
  font-weight: normal;
  font-size: .85em;
  color: #7e8890;
}
.mc-date-picker th,
.mc-date-picker td {
  text-align: center;
  padding: 0;
}
.mc-date-picker td {
  background: #fafafa;
  width: 30px;
  height: 30px;
}
.mc-date-picker td[aria-disabled=true] {
  color: #e2e2e2;
  cursor: not-allowed;
}
.mc-date-picker td[aria-disabled=false] {
  cursor: pointer;
  background: #eff2f7;
  color: #0078bc;
}
.mc-date-picker td[aria-disabled=false]:hover {
  background: #d3dff3;
}
.mc-date-picker td[data-selected=true] {
  background: #009cff !important;
  color: white !important;
}
.mc-date-picker td[data-today=true] {
  background: #d3dff3;
}
.mc-date-picker td[data-tooltip] {
  color: white;
}
.mc-date-picker-page {
  display: inline-block;
}
.mc-date-picker-nav button:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
}
.mc-date-picker-nav button:nth-child(2) {
  position: absolute;
  top: 0;
  right: 0;
}
.mc-drop-calendar {
  white-space: nowrap;
  font-size: 1em;
}
