.mc-slider input[type=range] {
  -webkit-appearance: none;
  cursor: pointer;
  height: 20px;
  margin: 4px 10px 4px 0;
  vertical-align: middle;
}
.mc-slider input[type=range][disabled] {
  opacity: 0.25;
  cursor: default;
  pointer-events: none;
}
.mc-slider input[type=range]:focus {
  outline: none;
}
.mc-slider input[type=range]::-webkit-slider-runnable-track {
  height: 6px;
  border-radius: 20px;
  background: #e8e8e8;
}
.mc-slider input[type=range]::-moz-range-track {
  height: 6px;
  border-radius: 20px;
  background: #e8e8e8;
}
.mc-slider input[type=range]::-ms-track {
  height: 6px;
  border-color: transparent;
  color: transparent;
  border-radius: 18px;
  background: #e8e8e8;
}
.mc-slider input[type=range]::-ms-fill-lower,
.mc-slider input[type=range]::-ms-fill-upper {
  background: none;
}
.mc-slider input[type=range]::-webkit-slider-thumb {
  border: 5px solid #009cff;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  background: white;
  -webkit-appearance: none;
  margin-top: -5px;
}
.mc-slider input[type=range]::-webkit-slider-thumb:active {
  transform: scale(1.2);
}
.mc-slider input[type=range]::-moz-range-thumb {
  border: 5px solid #009cff;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background: white;
}
.mc-slider input[type=range]::-moz-range-thumb:active {
  transform: scale(1.2);
}
.mc-slider input[type=range]::-ms-thumb {
  border: 5px solid #009cff;
  border-radius: 50%;
  background: white;
}
.mc-slider span {
  display: block;
}
