.mc-modal-scrim {
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  transition: opacity 150ms ease-out;
}
.mc-modal-scrim[aria-hidden=true] {
  display: none;
}
.mc-modal-dialog {
  overflow-y: scroll;
  background-color: white;
  box-shadow: 0 0 7px #444;
  outline: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto auto;
  transform-style: preserve-3d;
  border-radius: 4px;
}
.mc-modal-close {
  position: fixed;
  text-align: right;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 0;
  padding: 0;
}
.mc-modal-close button {
  margin: 10px 8px;
  padding: 12px;
}
.mc-modal-body {
  padding: 1.5em 3em 2em 1.75em;
  transform: translate3d(0, 0, 0);
}
.mc-modal-body[data-align=center] {
  padding: 1.5em 1.75em;
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  text-align: middle;
}
@media print {
  .mc-modal-scrim {
    position: static;
    background: none;
  }
  .mc-modal-dialog {
    position: static;
    overflow: visible;
    box-shadow: none;
    display: block;
    margin: 0 !important;
    width: 100% !important;
  }
  .mc-modal-body {
    padding: 0;
  }
}
