.mc-tabs {
  display: inline-block;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.mc-tabs li {
  display: inline-block;
}
.mc-tabs li[aria-checked=true] button,
.mc-tabs li[aria-selected=true] button {
  color: white;
  background: #009cff;
}
.mc-tabs li[aria-checked=true] button img,
.mc-tabs li[aria-selected=true] button img {
  filter: brightness(0) invert(100%);
}
.mc-tabs button {
  cursor: pointer;
  font-family: Helvetica;
  color: #009cff;
  margin: 0 .15em;
  padding: .30em 0.5em;
  border-radius: 2px;
}
.mc-tabs button img,
.mc-tabs button span {
  display: inline-block;
  vertical-align: middle;
}
.mc-tabs button img {
  margin: 0.15em 0.35em 0.15em 0;
  pointer-events: none;
}
.mc-tabs button[disabled] {
  opacity: 0.25;
  cursor: default;
}
.mc-tabs[data-rounded=true] button {
  padding: .35em 0.75em;
  border-radius: 2em;
}
