.mc-drop {
  display: inline-block;
  vertical-align: middle;
}
.mc-drop[data-align=right] .mc-drop-reveal {
  margin-left: 30px;
  transform: translateX(-100%);
}
.mc-drop-target {
  cursor: pointer;
  display: inline-block;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.mc-drop-reveal {
  position: absolute;
  z-index: 2000;
  text-align: left;
  margin-top: 8px;
}
.mc-drop-reveal[aria-hidden=true] {
  display: none;
}
.mc-drop-handle {
  font-weight: bold;
  color: #0078bc;
  padding: .5em .75em;
  white-space: nowrap;
  position: relative;
}
.mc-drop-handle:after {
  margin-left: .75em;
  content: url("data:image/svg+xml;base64,");
  display: inline-block;
  vertical-align: top;
}
.mc-drop-handle:active {
  top: 1px;
}
.mc-drop-content {
  background: white;
  box-shadow: 0 0 0 4px rgba(104, 105, 160, 0.15);
  border-radius: 4px;
  min-width: 100px;
  padding: .5em;
  font-size: 0.9em;
}
