.mc-form {
  margin-bottom: .75em;
  border-collapse: collapse;
}
.mc-form th,
.mc-form td {
  padding: 0 2em 0.75em 0;
  text-align: left;
  vertical-align: top;
}
.mc-form th {
  font-size: .9em;
  padding-top: 0.5em;
  white-space: nowrap;
}
.mc-form input,
.mc-form select {
  margin: 0 .5em .5em 0;
}
.mc-form-row {
  margin-bottom: 1.5em;
}
.mc-form-cell {
  display: inline-block;
  margin-right: 1.5em;
  vertical-align: top;
}
.mc-form-label {
  font-weight: bold;
  margin-bottom: 0.5em;
  font-size: 0.9em;
}
input[type=text],
input[type=password],
input[type=number],
textarea {
  font-size: 1em;
  padding: .5em;
  border: 1px solid #ccc;
}
input[type=text]:invalid,
input[type=password]:invalid,
input[type=number]:invalid,
textarea:invalid {
  box-shadow: 0 0 0 1px red;
}
input[type=number] {
  width: 75px;
}
input[type=radio] {
  margin-right: .5em;
  vertical-align: top;
}
input[type=range] {
  margin-right: 1em;
  vertical-align: top;
}
input[type=search] {
  border: 1px solid #ccc;
  font-size: 1em;
  padding: .6em .4em;
  border-radius: 25px;
  -webkit-appearance: none;
}
input[type=checkbox] {
  margin-right: 0.5em;
  vertical-align: middle;
}
select {
  font-size: 1em;
  height: 2em;
  line-height: 2em;
  border: 1px solid #ccc;
  background: white;
  margin-right: .5em;
}
label {
  margin-right: 0.5em;
}
.mc-form-feedback {
  color: red;
  font-weight: bold;
  margin-bottom: 1.5em;
}
.mc-form-controls {
  margin-top: 2em;
  white-space: nowrap;
}
.mc-cancel {
  background: none;
  color: #0078bc;
}
.mc-delete {
  background: none;
  color: #e0002b;
  font-weight: bold;
}
.mc-form-files > div {
  border: 1px dashed #999;
  padding: 1em;
  min-height: 100px;
  margin-bottom: 0.5em;
}
.mc-form-files > div[data-dragging=true] {
  background: #ebf7ff;
}
.mc-form-files input {
  display: none;
}
.mc-form-files li {
  margin: 1em 0;
}
.mc-form-files li > span {
  pointer-events: auto;
  padding: 0.35em 0.65em 0.5em 0.65em;
  background: #f0f6f9;
  border: 1px solid #aad6ed;
  border-radius: 1em;
}
.mc-form-files li > span span {
  vertical-align: middle;
}
.mc-form-files-target {
  pointer-events: none;
}
.mc-form-files-target > small {
  margin-bottom: 1em;
}
.mc-form-files-add {
  font-size: 0.9em;
}
