.mc-sidebar-layout > div {
  display: inline-block;
  vertical-align: top;
}
.mc-sidebar-layout > div:first-child {
  width: calc(100% - 260px);
}
.mc-sidebar-layout > div:last-child {
  width: 260px;
  padding-left: 20px;
}
.mc-sidebar-layout > div:last-child section {
  padding-bottom: 1.25em;
  margin-bottom: 1.25em;
}
.mc-sidebar-layout > div:last-child section:not(:last-child) {
  border-bottom: 1px solid #e8e8e8;
}
.mc-split-layout {
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
  justify-content: space-between;
  -ms-flex-pack: justify;
}
.mc-sticky {
  position: sticky;
  top: 25px;
}
@media print {
  .mc-sidebar-layout > div {
    display: block;
    width: auto !important;
    padding: 0 !important;
  }
}
